body {
  background-color: rgb(247, 167, 152);
}

.weather-container {
  text-align: center;

  max-width: 400px;
  height: auto;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: rgb(43, 131, 100);
  padding: 30px;
}

